.formInput{display:flex;flex-direction:column;flex-grow:1}.formInput label{font-size:14px;font-weight:600;color:#505f6f;margin-top:4px}.formInput input{outline:none;width:100%;border:1px solid #f5f7fb;padding:8px;border-radius:5px}.formInput input:focus{border:1px solid #0094c6}.formInput>div>select,.formInput select{outline:none;margin-left:8px;border:1px solid #f5f7fb;padding:8px;border-radius:5px}.formInput button{margin-top:16px;background-color:#0094c6;border:none;color:#fff;border-radius:5px;padding:8px}.formInput button:hover{background-color:rgba(0,148,198,.8)}